.items {
  /*FOR ITEMS*/
  padding: 10px;
  border: 1px solid black;
  background-color: white;
  border-radius: 0.5rem;
}

.top {
  /*FOR IMAGES, NAME, PRICE*/
  width: 100%;
  margin: 0 auto;
  padding-top: 20px;
}

.details {
  margin-top: 5px;
}

.bottom {
  /*FOR TEXT AND PRICE*/
  width: 100%;
  text-align: center;
  padding: 5px;
  font-size: 1.2rem;
}

.itemsImage {
  width: 100%;
  min-height: 12vh;
  max-height: 12vh;
  object-fit: contain;
  padding: 10px;
  background-color: #eee;
  border: 1px solid #eee;
}

.itemsBtns {
  margin: 5px 5px;
  padding: 5px;
}
