.cartContainer {
  width: 100%;
  text-align: center;
  padding: 5px;
}

table {
  border-collapse: collapse;
  table-layout: fixed;
  background-color: white;
  width: 100%;
  margin-bottom: 5rem;
}

table,
th,
td {
  width: 100%;
  border: 1px solid grey;
  margin: 10px auto;
  vertical-align: middle;
}

th {
  font-size: 0.7rem;
}

.emptyCart {
  border: none;
  text-align: center;
}
