* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
.App {
  background-color: #354358;
}

.wrapper {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  width: 100%;
  text-align: center;
}

.restaurant-main {
  background-color: #c8e6c9;
  border-radius: 0.5rem;
  margin: 15px 0;
  padding: 40px;
  width: 75%;
  height: fit-content;
}

.itemBoxContainerMain {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.label {
  text-align: center;
  color: darkcyan;
  padding: 10px;
}

.filterCategory {
  width: 80%;
  margin: 5px 0 5px 0;
  padding: 5px;
}

.cart {
  background-color: #c8e6c9;
  text-align: center;
  height: fit-content;
  width: 80%;
  padding: 25px;
  margin: 15px auto;
}

input,
.selectCategory {
  width: 100%;
  margin: 5px 0 5px 0;
  padding: 5px;
}

.modalBody {
  text-align: center;
}

.addItemBtn {
  margin-bottom: 5px;
}

.formBtn {
  margin: 15px auto;
  padding: 10px;
}

.cartBtns {
  margin: 5px;
}
